.blob-btn {
  z-index: 2;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid rgba(0, 0, 0, 0);
  outline: none;
  transition: color .5s, border .5s;
  position: relative;
}

.blob-btn--light {
  color: #4d1299;
  border: 2px solid #4d1299;
}

.blob-btn:before {
  content: "";
  z-index: 2;
  width: 100%;
  height: 100%;
  border-radius: 30px;
  position: absolute;
  top: 0;
  left: 0;
}

.blob-btn:after {
  content: "";
  z-index: -2;
  width: 100%;
  height: 100%;
  border-radius: 30px;
  transition: all .3s .2s;
  position: absolute;
  top: 3px;
  left: 3px;
}

.blob-btn:hover {
  color: #4d1299;
  border: 2px solid #4d1299;
}

.blob-btn:focus {
  color: #fff;
  background-color: #4d1299;
}

.blob-btn--light:hover {
  color: #fff;
  border: 2px solid #4d1299;
}

.blob-btn--white-border:hover {
  border: 2px solid #ebe7f3;
}

.blob-btn--light:focus {
  color: #4d1299;
  border: 2px solid #4d1299;
}

.blob-btn:hover:after {
  transition: all .3s;
  top: 0;
  left: 0;
}

.blob-btn__inner {
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: #4d1299;
  border-radius: 30px;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.blob-btn__inner--light {
  background-color: #ebe7f3;
}

.blob-btn__blobs {
  height: 100%;
  filter: url("#goo");
  display: block;
  position: relative;
}

.blob-btn__blob {
  width: 25%;
  height: 100%;
  background: #fff;
  border-radius: 100%;
  transition: transform .45s;
  position: absolute;
  top: 2px;
  transform: translate3d(0, 150%, 0)scale(1.7);
}

.blob-btn__blob--light {
  background: #4d1299;
}

@supports (filter: url("#goo")) {
  .blob-btn__blob {
    transform: translate3d(0, 150%, 0)scale(1.4);
  }
}

.blob-btn__blob:nth-child(1) {
  transition-delay: 0s;
  left: 0;
}

.blob-btn__blob:nth-child(2) {
  transition-delay: 80ms;
  left: 30%;
}

.blob-btn__blob:nth-child(3) {
  transition-delay: .16s;
  left: 60%;
}

.blob-btn__blob:nth-child(4) {
  transition-delay: .24s;
  left: 90%;
}

.blob-btn:hover .blob-btn__blob {
  transform: translateZ(0)scale(1.7);
}

@supports (filter: url("#goo")) {
  .blob-btn:hover .blob-btn__blob {
    transform: translateZ(0)scale(1.4);
  }
}

